<template>
<div class="popup poput-content" v-if="showPopup">
		<div class="skillImg">
			<img :src="skillItem[$store.state.dataLogic.skillIndex].image">
		</div>
		<div class="content-padded">
			<p style="text-indent: 1.5rem">{{skillItem[$store.state.dataLogic.skillIndex].Info}}</p>
		</div> 
		<div class="skill-btn">
			<p><a href="#" class="button button-fill close-popup">关闭</a></p>
		</div>
	</div>
</template>

<script>
	import skillOther from '../json/skillOther.js'
	export default{
		data(){
			return {
				skillItem:[],
				showPopup:false
			}
		},
		mounted(){
			var _this=this;
			_this.skillItem=skillOther;
			setTimeout(function(){
				_this.showPopup=true;
			},1000);
		}
	}

</script>

<style>
</style>



